.book-group {
  width: 20rem;
  height: 20rem;
  box-shadow: 0px 1px 5px -1px rgba(0, 0, 0, 0.35);
  

  .book-image {
    width: 100%;
    height: 15rem;
    overflow: hidden;

    img {
        width: 100%;
    }
  }

  .book-detail {
    padding: 0.5rem;

    .book-name {
        margin-bottom: 0.2rem;
      h4 {
        font-size: 1rem;
        margin: 0;
        padding: 0;
      }
    }
    
    .book-controls {
        display: flex;
        justify-content: center;
        align-items: flex-end;

        .book-links {
            margin: .2rem;
        }
    }
  }
}
