.sidebar-link {
  background-color: #ccc;
  margin-bottom: 1rem;
  transition: background-color 0.3s ease-in-out;

  &:hover {
      background-color: darken($color: rgb(199, 164, 164), $amount: 0.5);
  }

  a {
    width: 100%;
    height: 100%;
    display: block;
    padding: 1rem;
    text-decoration: none;
  }
}
