.dash-cards {
  padding: 1rem;
  border-radius: 4px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  border: 1px solid #ddd;
//   grid-column: span 2;
  color: #000;
  box-shadow: 0px 1px 5px -1px rgba(0, 0, 0, 0.35);
  transition: background-color 0.3s ease-in;

  p {
      font-size: 1rem;
      font-weight: 500;
  }

  &:hover {
      background-color: darken($color: #fff, $amount: 10);
  }
}
