.dashboard {
    padding: 2rem;
    display: grid;
    grid-auto-rows: 5rem;
    grid-gap: 2rem;
    grid-template-columns: repeat(3, 1fr);

    &-name {
        grid-column: 1 / -1;
        grid-row: 1 / 3;
        text-align: center;

        h2 {
            font-size: 3rem;
        }
    }

    .dashlinks {
        
    }
}