.getbooks {
  padding: 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;

  h2 {
    font-size: 2.5rem;
  }

  .search-bar {
    width: 80%;
    margin-bottom: 3rem;
    input {
      height: 3rem;
      width: 100%;
      margin-bottom: 1rem;
    }

    button {
      width: 100%;
      height: 3rem;
      border: none;
      cursor: pointer;
      transition: background-color 0.3s ease-in;

      &:hover {
        background-color: darken($color: #fff, $amount: 20);
      }
    }
  }

  .gotten-books {
      width: 80%;
      height: 65vh;
      overflow-y: scroll;
      display: grid;
      grid-template-columns: repeat(3, auto);
      justify-items: space-evenly;
      justify-content: space-between;
      grid-gap: 2rem;
  }
}
