.container-fluid {
  max-width: 1440px;
  margin: 0 auto;
}

.bordered {
  border: 1px solid #000;
}

.btn {
  margin-top: 3rem;
  a,
  button {
    background: none;
    border: none;
    text-decoration: none;
    background-color: #54b79d;
    padding: 1rem 2rem;
    border-radius: 3.5rem;
    font-size: 1rem;
    color: #fff;
  }
}
