.navbar {
//   height: 10vh;
  font-size: 0.9rem;
  box-shadow: 0px 5px 6px -3px rgba(0,0,0,0.25);

  &-main {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      flex: 1;
    }

    .navlinks {
      flex: 1;
      display: flex;
      justify-content: space-evenly;

      a {
        text-decoration: none;
      }
    }

    .navbar-access {
      flex: 1;
      display: flex;
      justify-content: flex-end;

      a {
        margin-left: 2rem;
        text-decoration: none;
      }
    }
  }
}
