.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90vh;

  &-img {
    flex: 1;

    img {
      width: 100%;
    }
  }

  &-container {
    flex: 1;
  }
  .hero-text {
    flex: 1;
    margin-left: 2rem;

    h1 {
      font-size: 3rem;
    }

    p {
      font-size: 1.2rem;
    }

    .btn {
      margin-top: 3rem;
      a {
        text-decoration: none;
        background-color: #54b79d;
        padding: 1rem 2rem;
        border-radius: 3.5rem;
        font-size: 1rem;
        color: #fff;
      }
    }
  }
}
